/* || 一般设置 */

html, body {
    margin: 0;
    padding: 0;
}
  
html {
    font-size: 10px;
    background: url(../images/南岭二教1.jpg);
}
  
body {
    width: 70%;
    min-width: 800px;
    margin: 0 auto;
}

/* || 排版 */
h1, h2, h3 {
    font-family: 'Long Cang', cursive;
    color: #ffff;
  }
  
  p, input, li {
    font-family: 'Noto Sans SC', sans-serif;
    color: #2a2a2a;
  }
  
  h1 {
    font-size: 4rem;
    text-align: center;
    text-shadow: 2px 2px 10px black;
  }
  
  h2 {
    font-size: 3rem;
  }
  
  h3 {
    font-size: 2.2rem;
  }
  
  p, li {
    font-size: 1.6rem;
    line-height: 1.5;
  }
  

/* || 标题布局 */
header {
    margin-bottom: 10px;
    display: flex;
    flex-flow: row wrap;
  }
  
  main, header, nav, article, aside, footer, section {
    background-color: #4169E1 ;
    padding: 1%;
  }
  
  h1 {
    flex: 5;
    text-transform: uppercase;
  }
  

  nav {
    height: 50px;
    flex: 100%;
    display: flex;
  }
  
  nav ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    flex: 2;
    display: flex;
  }
  
  nav li {
    display: inline;
    text-align: center;
    flex: 1;
  }

  nav ul #home {
    padding: 0;
    background-color: #1E90FF;
  }
  
  nav a, nav span {
    display: inline-block;
    font-size: 2rem;
    height: 3rem;
    line-height: 1.7;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
  }

